import styled from 'styled-components'
import { CSSTransition } from 'react-transition-group'
import { useState } from 'react'

const StyledCourses = styled.div`
	height: calc(100vh - 80px);

	/* slide classes */
	.slide-appear {
		opacity: 0;
		transform: translateY(-100px);
	}
	.slide-appear-active {
		opacity: 1;
		transform: translateY(0);
		transition: all 600ms;
	}
	.slide-enter {
		opacity: 0;
	}
	.slide-enter-active {
		opacity: 1;
		transition: opacity 2s;
	}
	.slide-exit {
		opacity: 1;
	}
	.slide-exit-active {
		opacity: 0;
		transition: opacity 2s;
	}
`
const Courses = () => {
	const [ inProp, setInProp ] = useState(true)

	return (
		<StyledCourses className="pt-32 md:pt-40   bg-gray-200">
			<div className="max-w-4xl mx-auto  px-4 xl:px-0">
				<CSSTransition in={inProp} timeout={2000} appear classNames="slide">
					<div className="py-6 md:py-0 h-20 rounded-xl shadow text-right bg-green-200"></div>
				</CSSTransition>
			</div>
		</StyledCourses>
	)
}
export default Courses
